/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import StateButton from '../../../components/StateButton';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import useLocalStorage from '../../../hooks/use-localstorage';
import instance from '../../../helpers/axiosInstance';
import IntlMessages from '../../../helpers/IntlMessages';
import { getHackathonInfo } from '../../../helpers/Utils';
import AppError from '../../../helpers/AppError';

import {
  adminRoot,
  hackathonDataStorageKey,
} from '../../../constants/defaultValues';

const Links = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please provide a Hackathon ID');
  const info = getHackathonInfo(id);

  const [hackathonData, setHackathonData] = useLocalStorage(
    `${hackathonDataStorageKey}_${id}`,
    info
  );

  const {
    hackathon_website,
    portfolio_url,
    contact_email,
    link_to_code_of_control,
    facebook,
    twitter,
    instagram,
    linkedin,
  } = hackathonData;

  const [formValues, setFormValues] = useState({
    hackathon_website,
    portfolio_url,
    contact_email,
    link_to_code_of_control,
    facebook,
    twitter,
    instagram,
    linkedin,
  });

  const handleChange = (event, key) => {
    setFormValues({
      ...formValues,
      [key]: event.target.value,
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const updateProfile = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/updateHackathon',
            data: { id, ...newData },
          });

          const { data } = response;
          if (data.success) {
            setHackathonData({ ...hackathonData, ...newData });
            success('Hackathon updated successfully!');
          } else {
            failed('Failed to update hackathon!');
          }
        } catch (error) {
          failed('Failed to update hackathon!');
        }
      };
      updateProfile();
    });
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.links" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="10" className="mb-4">
          <Card className="mb-4">
            <CardBody>
              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.hackathon-website" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="hackathon-website"
                  value={formValues.hackathon_website || ''}
                  onChange={(event) => {
                    handleChange(event, 'hackathon_website');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.portfolio-url" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="potfolio-website"
                  value={formValues.portfolio_url || ''}
                  onChange={(event) => {
                    handleChange(event, 'portfolio_url');
                  }}
                />
              </FormGroup>
            </CardBody>
          </Card>

          <Card className="mb-4">
            <CardBody>
              <FormGroup>
                <Label>
                  <IntlMessages id="challenges.contact-email" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="contact-email"
                  value={formValues.contact_email || ''}
                  onChange={(event) => {
                    handleChange(event, 'contact_email');
                  }}
                />
              </FormGroup>

              <FormGroup>
                <Label>
                  <IntlMessages id="challenges.link-to-code-of-control" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="link-to-code-of-control"
                  value={formValues.link_to_code_of_control || ''}
                  onChange={(event) => {
                    handleChange(event, 'link_to_code_of_control');
                  }}
                />
              </FormGroup>
            </CardBody>
          </Card>

          <Card className="mb-4">
            <CardBody>
              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.facebook" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="facebook"
                  value={formValues.facebook || ''}
                  onChange={(event) => {
                    handleChange(event, 'facebook');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.twitter" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="twitter"
                  value={formValues.twitter || ''}
                  onChange={(event) => {
                    handleChange(event, 'twitter');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.linkedin" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="linkedin"
                  value={formValues.linkedin || ''}
                  onChange={(event) => {
                    handleChange(event, 'linkedin');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.instagram" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="instagram"
                  value={formValues.instagram || ''}
                  onChange={(event) => {
                    handleChange(event, 'instagram');
                  }}
                />
              </FormGroup>
            </CardBody>
          </Card>

          <FormGroup className="text-center">
            <StateButton
              id="submitLinks"
              color="primary"
              type="submit"
              onClick={() => handleSubmit(formValues)}
            >
              <IntlMessages id="forms.submit" />
            </StateButton>
          </FormGroup>
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/application/${id}`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/brands/${id}`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

export default Links;
